<template>
  <v-app class="grey lighten-4">
    <!-- Root -->
    <Navbar></Navbar>
    <v-main>
      <!-- All content should be inside this -->
      <div class="try">
        <v-text-field class="text" label="Name" v-model="name"></v-text-field>

        <v-row>
          <v-col>
            <v-dialog
              ref="dialog"
              v-model="showDialog"
              :return-value.sync="date"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  class="text"
                  v-model="mdate"
                  label="Date"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="date"
                color="green lighten-1"
                :first-day-of-week="1"
                elevation="15"
              >
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="showDialog = false"
                  >Cancelar</v-btn
                >
                <v-btn text color="primary" @click="$refs.dialog.save(date)"
                  >Aceptar</v-btn
                >
              </v-date-picker>
            </v-dialog>
          </v-col>
        </v-row>

        <v-text-field
          class="text"
          label="Scores"
          v-model="scores"
        ></v-text-field>

        <v-btn
          large
          class="light-green lighten-3 black--text btn"
          :disabled="status1"
          @click="process"
          >GET RESULT</v-btn
        >

        <div class="try2">
          <p>Link:</p>
          <p style="text-decoration: underline; font-weight: 700">{{ link }}</p>
        </div>

        <v-btn
          large
          class="light-green lighten-3 black--text btn"
          :disabled="status2"
          @click="redirect"
          >REDIRECT TO!</v-btn
        >
      </div>
    </v-main>
  </v-app>
</template>


<script>
import Navbar from "@/components/Navbar";

export default {
  name: "App",
  components: {
    Navbar,
  },
  watch: {
    link: function () {
      if (this.link != "Not Availaible yet...") {
        this.status2 = false;
      } else {
        this.status2 = true;
      }
    },
    name: function () {
      if (this.name == "") {
        this.status1 = true;
      } else {
        if (this.date != "" && this.scores != "") {
          this.status1 = false;
        } else {
          this.status1 = true;
        }
      }
    },
    date: function () {
      if (this.date == "") {
        this.status1 = true;
      } else {
        let r = this.date.split("-");
        this.mdate = r[2] + r[1] + r[0];

        if (this.name != "" && this.scores != "") {
          this.status1 = false;
        } else {
          this.status1 = true;
        }
      }
    },
    scores: function () {
      if (this.scores == "") {
        this.status1 = true;
      } else {
        if (this.name != "" && this.date != "") {
          this.status1 = false;
        } else {
          this.status1 = true;
        }
      }
    },
  },
  data: function () {
    return {
      name: "",
      date: "",
      mdate: "",
      scores: "",
      link: "Not Availaible yet...",
      status1: true,
      status2: true,
      showDialog: false,
    };
  },
  methods: {
    redirect: function () {
      window.location.href = this.link;
      this.name = "";
      this.date = "";
      this.mdate = "";
      this.scores = "";
      this.link = "Not Availaible yet...";
      this.status1 = true;
      this.status2 = true;
      this.showDialog = false;
    },
    process: function () {
      let transformed = this.scores.replaceAll(";", "-");
      transformed = transformed.replace(/.$/, "_");

      let data = {
        jornada: this.mdate,
        partidos: "a_b_c_d_e_f_g_h_i_j_k_l_m_n_o",
        title: this.name,
        quiniela: transformed,
      };

      const fetch = require("node-fetch");
      const qs = require("qs");

      fetch(
        "https://cors-anywhere.herokuapp.com/http://quiniela.poquesoft.net/share.php",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          body: qs.stringify(data),
        }
      )
        .then((res) => {
          return res.text();
        })
        .then((data) => {
          this.link = data;
        });
    },
  },
};
</script>

<style>
@import "styles/style.css";
</style>
